import Layout from '../../components/AppLayout';
import ChatBox from "../../components/ChatBox";
import 'bootstrap/dist/css/bootstrap.min.css';
import { Button, Form, FormGroup, Input, InputGroup, InputGroupAddon, InputGroupText } from 'reactstrap';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faLiraSign, faUser, faMoneyBill, faUsers, faThumbsUp, faCalendarAlt } from '@fortawesome/free-solid-svg-icons'
import Link from "next/link";
import {auth, getUser} from "../../utils/auth";
const defaults = require('../../utils/defaults');
import {getSale} from "../../utils/api";
import Router from 'next/router'

class Sale extends React.Component{
    constructor(props) {
        super(props);
        let token = this.props.token;
        this.state = {
            loggedIn: !!token,
            user: {},
            sale: {},
            maxBid: {
                amount: 0,
                bidder: {
                    email: "",
                    nameSurname: ""
                }
            }
        };
    }
    componentDidMount() {
        getUser(this.props.token).then(user => {
            this.setState({
                user: user
            });
        });
        getSale(this.props.id).then(response => {
            if (!response){
                Router.push('/sales');
                return;
            }
            this.setState({
                sale: response.sale,
                maxBid: response.maxBid
            });
        });
    }

    render() {
        var loggedIn = false;
        return (
            <Layout page="me" user={this.state.user} loggedIn={this.state.loggedIn}>
                <div className="container bg-white" style={{ padding:'3%', marginTop:'3%', boxShadow: '0px 10px 5px 0px #ccc'}} >
                    <div className="row">
                        <div className="col-md-1"></div>
                        <div className="col-md-5">
                            <div className="mb-3" style={{maxHeight: '400px', overflow: 'hidden'}}>
                                <img src={this.state.sale.images[0]} width="100%" />
                            </div>
                            <h4> {this.state.sale.title} </h4>
                            <hr/>
                            <div className="row mt-4">

                                <p className="col-md-6 ">
                                    <FontAwesomeIcon icon={faUser} width="16" /> <b>Alıcı: </b>
                                    {this.state.maxBid.bidder.nameSurname}
                                </p>
                                <p className="col-md-6 ">
                                    <FontAwesomeIcon icon={faMoneyBill} width="16" /> <b>Satış fiyatı: </b> {this.state.maxBid.amount}₺
                                </p>
                                <p className="col-md-6">
                                    <FontAwesomeIcon icon={faUsers} width="16" /> <b>Teklif sayısı: </b> 13
                                </p>
                                <p className="col-md-6 mb-4">
                                    <FontAwesomeIcon icon={faCalendarAlt} width="16" /> <b>Kapanış: </b> 21 Kasım 2019
                                </p>
                            </div>
                        </div>
                        <div className="col-md-1"></div>
                        <div className="col-md-5">
                            <ChatBox/>
                        </div>
                    </div>
                </div>
            </Layout>
        );
    }
}

Sale.getInitialProps = async ctx => {
    const {id} = ctx.query;

    // Check user's session
    const token = auth(ctx);

    return { id, token }
};

export default Sale;